@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600;700&display=swap);
html{font-size:100%}body{margin:0;font-family:Open Sans, sans-serif}a:link,a:visited{text-decoration:none}ul{padding:0}nav ul li a,#home .content{color:white;-webkit-filter:drop-shadow(0 0 0.1rem black);filter:drop-shadow(0 0 0.1rem black)}nav{position:fixed;height:3.34375rem;width:100%;font-weight:700;transition:opacity 0.2s, background-color 0.2s;z-index:2}nav.invisible{opacity:0}nav ul{list-style:none;margin:0;text-align:center;white-space:nowrap}nav ul li{display:inline-block;padding:1rem 0}nav ul li a{padding:1rem 1rem;transition:color 0.2s}nav ul li a.active{color:orange}nav ul li a:hover{cursor:pointer}#nav-background{position:-webkit-sticky;position:sticky;top:0;height:3.34375rem;width:100%;margin-bottom:-3.34375rem;background-color:#161b22;z-index:1}section{display:flex;flex-direction:column;align-items:center;text-align:center}section .content{margin-left:2rem;margin-right:2rem;max-width:50rem}section .section-name{-webkit-margin-before:0;margin-block-start:0;padding-top:7.5rem;color:orange}#home{height:100vh;min-height:Max(55vw, 25rem)}#home>div{flex:1 1}#home #rain-image,#home #rain-video{position:absolute;object-fit:cover;object-position:50% 0%;width:100%;height:100vh;min-height:inherit}#home #rain-image{z-index:-2}#home #rain-video{position:fixed;z-index:-1}#home .content{display:flex;flex-direction:column;justify-content:center;padding:5rem 0;margin:auto;font-weight:700}#home .content #name,#home .content #roles{margin:0}#home .content #name{font-size:3rem}#home .content #roles{margin-bottom:0.25rem;font-size:0.9rem}#home .content #urls{font-size:3rem}#home .content #urls>*{color:inherit;margin:0 0.5rem}#home .content #urls>* svg,#home .content #urls>* path{color:inherit;transition:color 0.075s}#home .content #urls>*#icon-github:hover{color:#161b22}#home .content #urls>*#icon-linkedin:hover{color:#0a66c2}#home .content #urls>*#icon-mail:hover{color:#7d7}#home .content #wip{font-size:1.25rem}#home .content #wip *{margin:0 0.25rem}#about,#resume{font-weight:400}#about{background-color:#0d1117;color:#a7aeb5}#about a,#about h2,#about h3{color:white}#about p{text-align:left}#about #about-introduction *{max-width:50rem}#about #about-introduction * span{color:#c3c7cc}#about #about-info{max-width:50rem;margin-bottom:5rem}#about #about-info div{text-align:left}#resume{min-height:100vh;background-color:#f8f8f8}#resume a{color:orange}@media (min-width: 60vh){#home #rain-image,#home #rain-video{object-position:0% 100%}#about #about-info{display:flex;grid-gap:2.5rem;gap:2.5rem}#about #about-info div{flex:1 1}}

